// 异步打包
var WesiteCategory = r => require.ensure([], () => r(require('../pages/wesite/category')), 'wesite')
var WesitePost = r => require.ensure([], () => r(require('../pages/wesite/post')), 'wesite')
var WesitePostList = r => require.ensure([], () => r(require('../pages/wesite/post/List')), 'wesite')
var WesitePostEdit = r => require.ensure([], () => r(require('../pages/wesite/post/Edit')), 'wesite')
var WesiteMenu = r => require.ensure([], () => r(require('../pages/wesite/menu')), 'wesite')

export default [
// TODO: 所有的菜单均属于模块，不再手工添加路由
// module/:id
  {
    path: '/home',
    component: () => import('@/pages/layout/Default'),
    children: [
      {
        path: '/',
        name: 'home',
        component: () => import('@/pages/Home'),
        meta: {
          auth: true,
          name: '控制面板'
        }
      },
      {
        path: '/wesite/category',
        name: 'wesite_category',
        meta: {
          name: '微网站:分类管理'
        },
        component: WesiteCategory
      },
      {
        path: '/wesite/post',
        meta: {
          name: '微网站:文章管理'
        },
        component: WesitePost,
        children: [
          {
            path: '',
            name: 'wesite_post_list',
            component: WesitePostList,
            meta: {
              name: '微网站文章列表'
            }
          },
          {
            path: 'edit/:id',
            name: 'wesite_post_edit',
            component: WesitePostEdit,
            meta: {
              name: '修改微网站文章'
            }
          }
        ]
      },
      {
        path: '/wesite/menu',
        name: 'wesite_menu',
        meta: {
          name: '微网站:菜单管理'
        },
        component: WesiteMenu
      }
    ]
  },

  {
    path: '/login',
    name: 'login',
    component: () => import('@/pages/Login'),
    meta: {
      guest: true
    }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('@/pages/Register'),
    meta: {
      guest: true
    }
  },
  {
    path: '/',
    redirect: '/home'
  },

  {
    path: '/*',
    redirect: '/home'
  }
]



