import api from '../plugins/axios'

export const loading = ({commit}) => {
  commit('REQUEST_BEGIN')
}
export const loaded = ({commit}) => {
  commit('REQUEST_DONE')
}
export const toggleIconSelector = ({commit}) => {
  commit('ICON_SELECTOR_TOGGLED')
}
// Auth
export const authCheck = ({commit}) => {
  commit('AUTH_CHECK')
}
export const refreshToken = ({commit}) => {
  commit('REFRESH_TOKEN')
}
export const attemptLogin = async ({commit}, payload) => {
  const data = await api.post('auth', payload)
  if (data != null) {
    commit('LOGIN', data)
  }
}
export const logout = ({commit}) => {
  commit('LOGOUT')
}
// Account Find
export const findUser = async ({commit}) => {
  const data = await api.get('account', {include: 'menu'})
  if (data != null) {
    commit('FIND_USER', data)
  }
}



