import Vue from 'vue'

export const REQUEST_BEGIN = state => {
  return state.loading === true
}
export const REQUEST_DONE = state => {
  return state.loading === false
}
export const ICON_SELECTOR_TOGGLED = state => {
  state.showIcons = !state.showIcons
}
// Auth
export const REFRESH_TOKEN = (state, token) => {
  if (token == null) {
    this.LOGOUT(state)
    Vue.router.replace({
      name: 'login'
    })
  } else {
    state.authenticated = true
    localStorage.setItem('star_token', token.access_token)
    localStorage.setItem('refresh_token', token.refresh_token)
    Vue.$http.defaults.headers.common.Authorization = `Bearer ${token.access_token}`
  }
}
export const AUTH_CHECK = (state) => {
  let token = localStorage.getItem('star_token')
  state.authenticated = !!token
  if (state.authenticated) {
    Vue.$http.defaults.headers.common.Authorization = `Bearer ${token}`
  }
}
export const LOGIN = (state, token) => {
  state.authenticated = true
  localStorage.setItem('star_token', token.access_token)
  localStorage.setItem('refresh_token', token.refresh_token)
  Vue.$http.defaults.headers.common.Authorization = `Bearer ${token.access_token}`
  Vue.router.push({path: 'home'})
}
export const LOGOUT = (state) => {
  state.authenticated = false
  localStorage.removeItem('star_token')
  localStorage.removeItem('refresh_token')
  Vue.$http.defaults.headers.common.Authorization = ''
  Vue.router.replace({name: 'login'})
}
// ACCOUNT
export const FIND_USER = (state, account) => {
  let data = account.data
  state.account.email = data.email
  state.account.mobile = data.mobile
  state.account.name = data.name
  state.account.avatar = data.avatar
  state.account.last_login = data.last_login
  state.account.last_ip = data.last_ip
  state.account.nickname = data.nickname
  state.account.sex = data.sex
  state.account.wechat = data.wechat
  state.account.menus = data.menus
}



